html {
    --main-red: rgb(222, 43, 43);
    --main-dark-red: rgb(157, 26, 26);
    --main-orange: rgb(243, 167, 26);
}


@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

html, body, #root {
    width: 100%;
    height: 100%;
    margin: 0;
    font-family: 'Lato', sans-serif;
}

.App {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    overflow: auto;
}

.header {
    width: 100%;
    padding-top: 20px;
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    border-bottom: 2px solid var(--main-red);
}

.logo {
    cursor: pointer;
}

nav {
    position: relative;
    height: 80px;
    display: flex;
    align-items: flex-end;
    justify-content: space-around;
}

.nav-btn {
    width: 130px;
    height: 30px;
    margin-left: 40px;
    background-color: var(--main-red);
    color: var(--main-orange);
    font-size: 1.1em;
    font-weight: bolder;
    border: 2px solid var(--main-orange);
    border-bottom: none;
}

.nav-btn:hover {
    border: 2px solid var(--main-red);
    border-bottom: none;
    color: var(--main-red);
    background-color: var(--main-orange);
    cursor: pointer;
}

.nav-user {
    position: absolute;
    font-weight: bolder;
    color: var(--main-dark-red);
    right: 0;
    top: 0;
}

.nav-user-text:hover {
    color: var(--main-orange);
    cursor: pointer;
}

.nav-user-text {
    width: 200px;
    margin: 0;
    text-align: end;
}

.nav-user-logout {
    margin-top: 5px;
}

.main {
    padding-bottom: 100px;
}

.home {
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    width: 70%;
}

.home-section:nth-child(2n) {
    background-color: var(--main-red);
    color: var(--main-orange);
}

.hero {
    display: flex;
    justify-content: center;
    align-items: center;
}

.hero-adopt {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 100px;
    color: var(--main-dark-red);
}

.home-learn {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.home-learn img {
    width: 60%;
    height: 80%;
    box-shadow: 0px 0px 4px 4px var(--main-orange);
}

.home-learn-text {
    display: flex;
    flex-direction: column;
    width: 30%;
    height: 80%;
    font-size: 1.2em;
}

.home-btn {
    min-width: 150px;
    height: 40px;
    background-color: var(--main-orange);
    color: var(--main-red);
    font-size: 1em;
    font-weight: bolder;
    border: 2px solid var(--main-dark-red);
}

.home-btn:hover {
    color: var(--main-orange);
    background-color: var(--main-red);
    cursor: pointer;
}

.about {
    width: 70%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    margin: 30px;
}

.about img {
    width: 40%;
    box-shadow: 0px 0px 3px 3px var(--main-dark-red);
}

.about-text {
    width: 45%;
    padding: 20px;
    padding-bottom: 40px;
    border: 2px solid var(--main-orange);
    color: var(--main-orange);
    background-color: var(--main-red);
    font-size: 1.3em;
}

.browse-container {
    width: 65%;
    height: 600px;
    margin-top: 50px;
    padding-bottom: 200px;
    position: relative;
    
}

.browse {
    width: 100%;
    height:600px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px solid var(--main-orange);
    background-color: var(--main-dark-red);
    color: var(--main-orange);
    font-weight: bolder;
    position: relative;
}

.browse-search {
    width: 90%;
    height: 30px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 50px;
}

.browse-search input {
    width: 90%;
    height: 100%;
    padding-left: 20px;
}


.browse-listings {
    width: 100%;
    height: 520px;
    background-color: var(--main-red);
    display: grid;
    gap: 40px;
    grid-template-columns: repeat(2, 1fr);
    overflow: auto;
    position: absolute;
    padding-bottom: 20px;
    bottom: 0;
    border-top: 2px solid var(--main-orange);
}

.browse-listings > div {
    line-height: 30px;
}

.browse-open-listing-container {
    width: 100%;
    height: 70%;
    background-color: var(--main-dark-red);
    border: 4px solid var(--main-orange);
    padding: 40px;
    position: absolute;
    z-index: 1;
    top: 0;
}

.browse-open-listing {
    width: 100%;
    height: 100%;
    display: flex;
}

.browse-open-listing-pic-container {
    height: 100%;
    border: 2px solid var(--main-orange);
    display: flex;
    justify-content: center;
    background-color: black;
}

.browse-open-listing-container img{
    height: 100%;
    max-width: 100%;
}

.browse-open-listing-close-btn {
    position: absolute;
    right: 10px;
    top: 10px;
    font-size: 1.5em;
    font-weight: bolder;
    background-color: var(--main-dark-red);
    border: none;
    color: var(--main-orange);
}

.browse-open-listing-close-btn:hover {
    cursor: pointer;
    color: var(--main-red);
}

.browse-open-listing-info {
    width: 50%;
    margin-left: 40px;
    font-size: 1.2em;
    line-height: 30px;
    overflow: auto;
}

.browse-open-listing-info p {
    width: 95%;
    background-color: var(--main-orange);
    color: var(--main-dark-red);
    padding: 5px;
    border: 2px solid var(--main-red);
}

.browse-btn {
    height: 40px;
    border: 2px solid var(--main-orange);
    background-color: var(--main-red);
    align-self: flex-end;
    margin-right: 9px;
    color: var(--main-orange);
    font-weight: bolder;
    font-size: 1.1em;
}

.browse-btn:hover {
    color: var(--main-dark-red);
    background-color: var(--main-orange);
    cursor: pointer;
}   

.message-window {
    position: absolute;
    width: 80%;
    height: 80%;
    border: 2px solid var(--main-orange);
    top: 10%;
    left: 10%;
    background-color: var(--main-dark-red);
    display: flex;
    justify-content: center;
    align-content: center;
}

.message-window form {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    width: 50%;
}

.message-window form * {
    margin-top: 10px;
}

.message-window textarea {
    height: 200px;
}

.message-sent {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}   

.message-window-close-btn {
    position: absolute;
    right: 0;
    font-size: 1.7em;
    color: var(--main-orange);
    background-color: var(--main-dark-red);
    border: none;
}

.message-window-close-btn:hover {
    color: var(--main-red);
    cursor: pointer;
}

.contact {
    width: 80%;
    height: 70%;
    display: flex;
    justify-content: space-around;
    padding-top: 50px;
}

.contact-social {
    width: 40%;
    height: fit-content;
    padding: 20px;
    background-color: var(--main-red);
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px solid var(--main-orange);
    color: var(--main-orange);
}

.contact-social-section {
    display: flex;
    align-items: center;
    width: 60%;
    padding-left: 15%;
    padding-right: 15%;
    justify-content: space-between;
    font-size: 1.5em;
    margin-top: 10px;
    margin-bottom: 10px;
    text-decoration: none;
    color: var(--main-orange);
    border-bottom: 3px solid var(--main-dark-red);
    padding-bottom: 10px;
    padding-top: 5px;
}

.contact-social-section:hover {
    background-color: var(--main-orange);
    color: var(--main-dark-red);
    cursor: pointer;
}

.contact-form {
    width: 40%;
    height: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--main-red);
    border: 2px solid var(--main-orange);
    color: var(--main-orange);
}

.contact-form form {
    display: flex;
    flex-direction: column;
    width: 80%;
    font-weight: bolder;
    font-size: 1.1em;
}

.contact-form form * {
    margin-top: 10px;
}

.contact-form textarea {
    height: 200px;
}

.contact-form button {
    margin-top: 20px;
    align-self: flex-end;
}

.listing {
    display: flex;
    height: 150px;
    margin-bottom: 10px;
    margin-top: 10px;
    padding: 10px;
    background-color: var(--main-orange);
    color: var(--main-dark-red);
}

.listing:hover {
    background-color: white;
    cursor: pointer;
}

.listing-pic {
    width: 150px;
    display: flex;
    justify-content: center;
}

.listing-pic-img {
    height: 100%;
    max-width: 100%;
}

.listing-info {
    margin-left: 10px;
    height: 100%;
}

.listing-section {
    height: 25%;
}



.login {
    width: 75%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    padding-top: 50px;
}

.login h2 {
    width: 100%;
    text-align: center;
}

.login-section {
    margin-top: 50px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 20px;
    background-color: var(--main-red);
    color: var(--main-orange);
    font-weight: bolder;
    font-size: 1.1em;
    border: 1px solid red;
}

.login-label {
    margin-bottom: 10px;
}

.login-label input {
    margin-left: 10px;
    width: 300px;
    height: 30px;
}

.login-checkbox-container {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-checkbox-container label {
    margin: 0;
}

.login-checkbox {
    width: 20px;
    height: 20px;
}

.login-checkbox input {
    margin: 0;
}

.profile {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile-container {
    width: 85%;
    height: 90%;
    margin-top: 50px;
    background-color: var(--main-dark-red);
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 20px;
    border: 2px solid var(--main-orange);
}

.profile-sidebar {
    padding: 10px;
    width: 35%;
    height: fit-content;
    background-color: var(--main-red);
    display: flex;
    flex-direction: column;
    border: 2px solid var(--main-orange);
}

.profile-pic-container {
    margin-top: 20px;
    margin-bottom: 20px;
    height: 200px;
    width: 200px;
    border: 2px solid var(--main-dark-red);
    border-radius: 50%;
    align-self: center;
    position: relative;
}

.upload-img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    color: var(--main-orange);
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.profile-pic {
    position: absolute;
    border-radius: 50%;
    height: 100%;
    width: 100%;
    top: 0;
}

.upload-img:hover .upload-img-overlay{
    visibility: visible;
    cursor: pointer;
}

.upload-img-overlay-btn:hover {
    cursor: pointer;
}

.upload-profile-pic {
    z-index: 0;
    width: 100%;
    margin: 10px;
}

.upload-img-overlay {
    z-index: 1;
    visibility: hidden;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: rgba(255, 198, 10, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
}

.upload-img-overlay button {
    background-color: rgba(135, 134, 134, 0.01);
    border: none;
    color: var(--main-dark-red);
    font-size: 1.2em;
}

.profile-data-show-container {
    width: 95%;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--main-orange);
    margin-top: 5px;
    margin-bottom: 5px;
    color: var(--main-dark-red);
    border: 2px solid var(--main-dark-red);
}

.profile-data-show-text {
    width: 85%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: bolder;
    margin-left: 10px;
    margin-right: 10px;
}

.profile-data-show-text p {
    overflow: auto;
    width: 70%;
}

.profile-data-show-container:hover .profile-data-show-btn {
    visibility: visible;
    cursor: pointer;
}

.profile-data-show-btn {
    visibility: hidden;
    background-color: var(--main-orange);
    border: none;
    color: var(--main-dark-red);
}

.profile-sidebar-edit-btn {
    background-color: var(--main-orange);
    border: none;
    color: var(--main-dark-red);
    font-weight: bolder;
}

.profile-sidebar-edit-btn:hover {
    color: var(--main-red);
    background-color: var(--main-orange);
    cursor: pointer;
}

.profile-data-show-text label {
    margin: 0;
    margin-right: 10px;
    font-size: 1.1em;
}

.profile-data-show-text p {
    margin: 0;
    font-size: 1.1em;
}

.profile-data-show-container input {
    width: 80%;
    height: 70%;
    margin-left: 1%;
    padding-left: 2%;
    font-size: 1.1em;
}

.profile-content {
    width: 60%;
    height: 90%;
    background-color: var(--main-red);
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px solid var(--main-orange);
}

.profile-content h2 {
    color: var(--main-orange);
}

.user-listing {
    border: 2px solid var(--main-orange);
    min-height: 150px;
    max-height: 200px;
    display: flex;
    margin: 20px;
    background-color: var(--main-orange);
    color: var(--main-dark-red);
    font-size: 1.1em;
}

.user-listing img {
    height: 100%;
    width: auto;
}

.user-listing:hover {
    background-color: white;
    cursor: pointer;
}

.user-listing-pic {
    width: 250px;
    margin-right: 20px;
}

.user-listing-info {
    width: 80%;
    height: 150px;
    padding-bottom: 20px;
    overflow: auto;
    display: flex;
    flex-direction: column;
}

.user-listing-section {
    margin-top: 5px;
}

.user-listing-section label {
    font-weight: bolder;
}

.footer {
    width: 100%;
    height: 50px;
    display: flex;
    position: fixed;
    bottom: 0;
    justify-content: center;
    align-items: center;
    background-color: var(--main-red);
    border-top: 2px solid var(--main-orange);
    color: var(--main-orange);
    font-weight: bolder;
    margin-top: 20px;
}